import React, { useEffect } from "react";

import ExpandLess from "@mui/icons-material/ExpandLess";
import ExpandMore from "@mui/icons-material/ExpandMore";
import Box from "@mui/material/Box";
import Collapse from "@mui/material/Collapse";
import List from "@mui/material/List";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import MenuList from "@mui/material/MenuList";
import { useTheme } from "@mui/material/styles";
import Typography from "@mui/material/Typography";
import useMediaQuery from "@mui/material/useMediaQuery";

import { filter, get, map } from "lodash";
import { NavLink } from "react-router-dom";

import ComponentStyle from "../../../styles/ComponentStyle.module.css";
import MenuItemsList from "./MenuItemsList";
import translate from "../../../components/hooks/translate";

const SubMenuLayout = (props) => {
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);

  const matchUpMd = useMediaQuery(theme.breakpoints.up("md"));

  const subMenuItems = filter(
    MenuItemsList,
    (item) => item.path === props.pathName
  );

  const handleClick = () => {
    setOpen(!open);
  };

  useEffect(() => {
    matchUpMd ? setOpen(true) : setOpen(false);
  }, [matchUpMd]);

  return (
    <>
      <MenuList className="pt-0">
        {!matchUpMd && (
          <ListItemButton
            className={ComponentStyle.submenu_button}
            onClick={handleClick}
          >
            <ListItemText />
            {open ? <ExpandLess /> : <ExpandMore />}
          </ListItemButton>
        )}

        <Collapse in={open} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            {map(get(subMenuItems, "[0].subMenu", []), (item, index) => (
              <Box key={index} className={ComponentStyle.profile_steps}>
                <NavLink to={item.path}>
                  {({ isActive }) => (
                    <ListItemButton
                      className={
                        isActive
                          ? ComponentStyle.sidebar_menu_active
                          : `${ComponentStyle.sidebar_menu} nav-links`
                      }
                      selected={isActive}
                      sx={{
                        px: 1,
                        py: 1.2,
                        marginBottom: "8px",
                        background: "#FFFFFF",
                        boxShadow: "0px 0px 2px rgba(145, 158, 171, 0.2)",
                        borderRadius: "5px",
                        color: "text.secondary",
                        "&:hover": {
                          color: "primary.main",
                          backgroundColor: "primary.lighter",
                        },
                        "&.Mui-selected": {
                          color: "primary.main",
                          backgroundColor: "primary.lighter",
                          "&:hover": {
                            color: "primary.main",
                            backgroundColor: "primary.lighter",
                          },
                        },
                      }}
                      key={index}
                    >
                      <ListItemIcon>{item.icon}</ListItemIcon>
                      <Typography variant="body2" className="text-break">
                        {translate(item?.label)}
                      </Typography>
                    </ListItemButton>
                  )}
                </NavLink>
              </Box>
            ))}
          </List>
        </Collapse>
      </MenuList>
    </>
  );
};

export default SubMenuLayout;
